.App {
  text-align: center;
}

.App-header {
  position: fixed;
  top: 0;
  left: 0;
  padding-top: 16px;
  padding-bottom: 16px;
  width: 100%;
  background-color: #282c34;
}

.App-header a {
  margin: 10px;
  text-decoration: none;
  color: #61dafb;
}

.App-header a:hover {
  color: #337ab7;
}

.align-left {
  text-align: left;
}

.highlight-light {
  color: #ffcccc;
}

.highlight-dark {
  color: #337ab7;
}