#work {
    background-color: white;
    color: #282c34
}

.work {
    padding: 0 30px;
    max-width: 800px;
}

.work h1 {
    font-size: 2em;
    font-weight: 500;
}

.work .sub-title {
    margin: 12px 0 0 0;
}

.work .content-block {
    display: block;
    margin-top: 60px;
}

.button {
    display: inline-block;
    padding: 5px 10px;
    text-decoration: none;
    background-image: linear-gradient(to right,#282c34,#337ab7);
    color: white;
    border-radius: 15px;
    transition: transform .2s;
}

.button:hover {
    transform: scale(1.05);
}