.section {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    min-height: 100vh;
    padding: 30px 0;
    background-color: #282c34;
    color: white;
}

.scroll-down {
    transition: transform .2s;
}

.scroll-down:hover {
    transform: scale(1.1);
}